@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,500;0,900;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;width:100vw;box-sizing:border-box;margin:0;overflow:hidden;position:relative;background-color:#d6e0f0;font-family:'Roboto'}

.LandingPage{height:80%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-end}.LandingPage h1{text-align:center;font-size:3rem}.LandingPage .auth-container{display:flex;justify-content:space-evenly;align-items:center;width:100%;height:40%}.LandingPage .auth-container div{width:40%;height:80%;background-color:white;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:20px;font-size:1rem;text-align:center}.LandingPage .auth-container div a{text-decoration:none}

.SignUp{display:flex;align-items:center;flex-direction:column}.SignUp form{border:1px solid lime;width:20rem;height:20rem;display:flex;align-items:center;flex-direction:column;justify-content:space-evenly}.SignUp form label{display:flex;flex-direction:column}

.Login{display:flex;align-items:center;flex-direction:column;width:100vw;height:100vh;background-color:#d6e0f0}.Login form{border-radius:20px;height:20rem;width:20rem;display:flex;align-items:center;flex-direction:column;justify-content:space-evenly;background-color:#8d93ab}.Login form label{display:flex;flex-direction:column}

.Logo a{display:flex;text-decoration:none;align-items:center;justify-content:flex-start}.Logo a svg{margin:0;transform:scale(0.5)}.Logo a svg .lock{animation:lock .5s ease-in forwards}.Logo a h2{font-size:2rem;color:white;transform:translateX(-10px)}.Logo a h2 span{font-weight:100}@keyframes lock{from{transform:translateY(-20%)}to{transform:translateY(5%)}}

.HeaderProfile{display:flex;justify-content:space-between;height:3rem;width:15rem;border:1px solid white;border-radius:10px;align-items:center;overflow:hidden;position:absolute;right:2.5%;transition:all .75s ease-in;opacity:1}.HeaderProfile p{color:white;padding:.5rem;overflow:hidden;font-weight:700}.HeaderProfile button{width:40%;height:100%;color:white;background-color:#333333}@media screen and (max-width: 800px){.HeaderProfile{display:none}}

.Header{display:flex;justify-content:center;align-items:center;width:100%;height:15%;background-color:#393b44;overflow:hidden;transition:all ease-in .2s}@media screen and (max-width: 800px){.Header{display:flex;justify-content:center;align-items:center;width:100%;height:10%;background-color:#393b44;transition:all ease-out .5s}.Header .Logo{transform:scale(0.75)}}

.Profile{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;animation:fade-in 2s forwards;margin-top:1rem;position:relative}.Profile .profile-photo{width:100px;height:100px;border-radius:100%;border:3px solid white;box-shadow:0px 0px 15px rgba(0,0,0,0.5)}.Profile .add-link-button{position:absolute;top:5%;right:5%}.Profile h1{margin:0;padding:0;color:#444444}.Profile h1 span{opacity:.5}@keyframes fade-in{from{opacity:0}to{opacity:1}}

.LinkItem{border:3px solid white;border-radius:5px;box-shadow:0px 0px 15px rgba(0,0,0,0.25);height:5rem;width:45%;margin:.5rem;background-color:white;position:relative}.LinkItem div{width:100%;height:100%;text-decoration:none;display:flex;align-items:center;justify-content:center;cursor:pointer}.LinkItem div p{color:grey}.LinkItem .lock{height:25px;width:25px;position:absolute;right:1%;top:35%}.LinkItem .locked{opacity:1}.LinkItem .unlocked{opacity:0}@media screen and (max-width: 800px){.LinkItem{border:3px solid white;border-radius:5px;box-shadow:0px 0px 15px rgba(0,0,0,0.25);height:5rem;width:90%;margin:.5rem;background-color:white}.LinkItem div{width:100%;height:100%;text-decoration:none;display:flex;align-items:center;justify-content:center;cursor:pointer}.LinkItem div p{color:grey}.LinkItem .lock{height:25px;width:25px;position:absolute;right:1%}.LinkItem .locked{opacity:1}.LinkItem .unlocked{opacity:0}}

.LinksDisplay{width:100%;height:100%;display:flex;justify-content:center;position:relative}.LinksDisplay ul{border-radius:10px;border:2px solid grey;background-color:rgba(144,144,144,0.1);list-style:none;display:flex;width:90%;height:80%;justify-content:center;flex-wrap:wrap;overflow-y:scroll;overflow-x:hidden;padding:1rem 0}

.Loading{position:absolute;height:100vh;width:100vw;display:flex;justify-content:center;align-items:center}

.LinkPasswordForm{position:absolute;background-color:#444444}.closed{opacity:0}

.LinkForm{border:2px solid grey;position:absolute;background-color:#393B44;border-radius:20px;height:80%;width:90%;color:white;display:flex;flex-direction:column;justify-content:center;align-items:center}.LinkForm .close-form-button{position:absolute;top:5%;right:5%;border-color:white;color:white;font-weight:900;font-size:1rem;transition:all ease-out .3s}.LinkForm .close-form-button:hover{transition:all ease-in .3s;color:rgba(255,83,83,0.5);transform:scale(1.05)}.LinkForm h2{height:10%}.LinkForm form{width:80%;height:100%;color:white}.LinkForm form input{color:white;outline-color:white}.LinkForm form label{display:flex;flex-direction:column;margin:.75rem 0}.LinkForm form .checkbox{margin:0;display:flex;flex-direction:row;align-items:center}.LinkForm form .password{position:relative;transition:all .25s ease-in}.LinkForm form .password .toggle{position:absolute;right:5%;top:50%;margin:auto 0}.LinkForm form .password .toggle svg{transform:scale(1.25);fill:rgba(255,255,255,0.5)}.LinkForm form .disabled{opacity:0.1;transition:all .25s ease-out}.LinkForm form .submit-button{margin-top:1rem;color:white;border:2px solid white}.open{opacity:1;transform:translateY(0%);transition:all .5s ease-in}.closed{opacity:0;transform:translateY(100%);transition:all .5s ease-in;pointer-events:none}

