.LinksDisplay {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  position: relative;

  ul {
    border-radius: 10px;
    border: 2px solid grey;
    background-color: rgba(144, 144, 144, 0.1);
    list-style: none;
    display: flex;
    width: 90%;
    height: 80%;
    justify-content: center;
    flex-wrap: wrap;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 1rem 0;
  }
}
