.Profile {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fade-in 2s forwards;
  margin-top: 1rem;
  position: relative;

  .profile-photo {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    border: 3px solid white;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5);
  }

  .add-link-button {
    position: absolute;
    top: 5%;
    right: 5%; 
  }

  h1  {
    margin: 0;
    padding: 0;
    color: #444444;

    span {
      opacity: .5;
    }
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
